
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import CarList from "./CarList";
export default {
props: ["heightm","userid","driverInfo" ],
data() {
var dataset = {
index: 0,
ys:"",
type:"4"
};
dataset.ys="height:20vh;";
return dataset;
},
computed: {},
created() {},
watch: {

},
methods: {
onItemClick(index) {
this.index = index;
if(this.index==0)this.index=4;
this.type=this.index.toString();
}
},
components: {
Tab,
TabItem,
CarList
}
};



