
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import {currentdriverinfo} from "@/api/driver";
import CarItem from "./CarItem";
import setting from "../setting/main";


export default {
created(){
var dataset=this;
currentdriverinfo().then(function(result){
if(result==null||result=="")return;
dataset.driverInfo=result;
dataset.userid=result.userid;
});
},
data() {
var dataset = {
titleEnum: ["主页", "我的信息"],
pageIndex: 0,
height: 0,
userid:0,
driverInfo:{}
};
this.clientHeight = `${document.documentElement.clientHeight}`;
dataset.height= this.clientHeight-84;
return dataset;
},
methods: {
onItem(n) {
this.pageIndex = n;
}
},
computed: {},
components: {
Tabbar,
TabbarItem,
Group,
Cell,
XHeader,
CarItem,
setting

}
};



